import * as React from 'react';
import styled from 'styled-components';
import ScrollIntoView from 'react-scroll-into-view';
import { device } from 'lib/constants';
import { Content } from 'content';
import { ButtonSubscribe } from 'ui/molecules';

interface PositionsProps {
  from: number;
  to: number;
  link: string;
}

export const Menu: React.FC<{ items: { text: string; link: string }[] }> = ({
  items,
}) => (
  <Nav>
    <MenuItems items={items} className="menu" />
    <ButtonMenu>
      <ButtonSubscribe>{Content.buttonMenuText}</ButtonSubscribe>
    </ButtonMenu>
  </Nav>
);

export const MenuItems: React.FC<{
  className: string;
  items: { text: string; link: string }[];
  onMenuClick?(event?: React.SyntheticEvent): void;
}> = ({ items, className, onMenuClick }) => {
  const [activeMenu, setActiveMenu] = React.useState('#main');

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  });

  const getMenuItem = (position: number) => {
    const allPages = document.getElementsByClassName('page');
    const pages: PositionsProps[] = [];

    [].forEach.call(allPages, (el: HTMLElement) => {
      pages.push({
        from: el.offsetTop,
        to: el.offsetTop + el.offsetHeight,
        link: '#' + el.dataset.id,
      });
    });

    const page = pages.find((el) => el.from < position && position < el.to);
    return page;
  };

  const handleScroll = () => {
    const { scrollY, innerHeight } = window;

    const page = getMenuItem(scrollY + innerHeight / 2);
    if (page) {
      setActiveMenu(page.link);
    }
  };

  return (
    <MenuBlock className={className}>
      {items.map(({ text, link }, key) => (
        <ScrollIntoView selector={link} key={key}>
          <MenuItem
            onClick={() => {
              if (onMenuClick) onMenuClick();
            }}
            className={activeMenu === link ? 'active' : ''}
          >
            <MenuLink>{text}</MenuLink>
          </MenuItem>
        </ScrollIntoView>
      ))}
    </MenuBlock>
  );
};

export const MenuBlock = styled.ul`
  padding-left: 15px;
  margin: 0;

  @media ${device.tablet} {
    display: flex;
  }
  @media ${device.desktop} {
    padding-left: 135px;
  }
`;

export const MenuItem = styled.li`
  list-style-type: none;
  padding-right: 45px;
  &:hover {
    cursor: pointer;
  }
  &.active {
    a {
      text-shadow: 0 0 12px rgba(255, 248, 221, 0.55);
      background-image: radial-gradient(
        circle at 44% 50%,
        #ffbf5d,
        #ff9a00 90%
      );
      font-size: 12px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      opacity: 1;
    }
  }
`;

export const MenuLink = styled.a`
  opacity: 0.6;
  color: var(--primary);
  font-size: 12px;
  font-weight: 800;
  line-height: 1.75;
  text-decoration: none;
`;

export const Nav = styled.nav`
  padding: 24px 0;
  display: none;
  width: 100%;
  align-items: center;
  justify-content: space-between;

  @media ${device.tablet} {
    margin: 0 auto;
    display: flex;
    max-width: 768px;
  }

  @media ${device.desktop} {
    max-width: 1440px;
  }
`;

export const ButtonMenu = styled.div`
  display: none;
  padding-right: 64px;

  button {
    height: 48px;
    font-size: 16px;
    width: 170px;
    padding: 0 10px;
  }
`;
