import * as React from 'react';
import {
  ButtonBlock,
  ButtonDescription,
  Cloud,
  CloudImg,
  CosmonautImg,
  Description,
  Header,
  ShapeLeft,
  ShapeRight,
  StarMathPage,
  TopBlock,
} from './style';
import { Title, TextBody } from 'ui/atoms';
import { Sidebar, ButtonSubscribe } from 'ui/molecules';
import { Layout } from 'ui/templates';
import { Menu } from './menu';
import { bgStarMath, bgTabletStarMath } from 'assets/images/background';
import { shapeOneLeft, shapeOneRight, Clouds } from 'assets/images/svg';
import cosmonaut from 'assets/images/png/cosmonaut.png';
import { Content } from 'content';
import { starMathEvent } from 'lib/events';

const menu = [
  { text: Content.menuStarMath, link: '#main' },
  { text: Content.menuGames, link: '#games' },
  { text: Content.menuOurMethod, link: '#method' },
  { text: Content.menuOurGoal, link: '#goal' },
  { text: Content.menuSubscribe, link: '#subscribe' },
];

export const StarMath: React.FC = () => {
  const [isVisible, setVisible] = React.useState(false);
  const [menuView, setMenuView] = React.useState<'fixed' | null>(null);

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const handleScroll = () => {
    const { scrollY } = window;
    if (scrollY > 121) {
      setMenuView('fixed');
    }
    if (scrollY < 120) {
      setMenuView(null);
    }
  };
  return (
    <StarMathPage className="page" data-id="main">
      <TopBlock scrollData={menuView}>
        <Menu items={menu} />
        <Sidebar
          isVisible={isVisible}
          toggler={() => {
            setVisible(!isVisible);
          }}
          menu={menu}
        />
      </TopBlock>
      <Layout
        background={bgStarMath}
        smallBackground={bgTabletStarMath}
        id="main"
      >
        <Cloud>
          <CloudImg src={Clouds} />
        </Cloud>
        <Header>
          <Title size="l">{Content.starMathTitle}</Title>
        </Header>
        <Description>
          <TextBody color="basic">{Content.starMathText}</TextBody>
        </Description>
        <ButtonBlock onClick={starMathEvent}>
          <ButtonSubscribe>{Content.buttonText}</ButtonSubscribe>
        </ButtonBlock>
        <ButtonDescription>
          <TextBody color="secondary" size="s">
            {Content.starMathBottomText}
          </TextBody>
        </ButtonDescription>
        <CosmonautImg src={cosmonaut} />
      </Layout>
      <ShapeLeft src={shapeOneLeft} />
      <ShapeRight src={shapeOneRight} />
    </StarMathPage>
  );
};
